@import "../node_modules/bootstrap/dist/css/bootstrap.css";
@import "../node_modules/bootstrap/dist/css/bootstrap-utilities.min.css";

@font-face {
    font-family: 'poppins';
    src: url(assets/fonts/Poppins-Regular.ttf);
}

@font-face {
    font-family: 'nunito';
    src: url(assets/fonts/NunitoSans.ttf);
}

*{
    font-family: 'nunito';
}

.ant-table-thead > tr > th {
    position: relative;
    color: rgba(0, 0, 0, 0.85);
    font-weight: 500;
    text-align: left;
    background: #ebebeb;
    border-bottom: 1px solid #f0f0f0;
    transition: background 0.3s ease;
}

.mr-05{
    margin-right: 0.5rem;
}

.error{
    border: 1px solid red !important;
}

.success-chip{
    padding: 4px 17px;
    border-radius: 7px;
    background-color: #f6ffed;
    border: 1px solid #b7eb8f;
}


.danger-chip{
    padding: 4px 17px;
    border-radius: 7px;
    background-color: #fff2f0;
    border: 1px solid #ffccc7;
}

.card{
    box-shadow: 0px 0px 35px 0px rgb(154 161 171 / 19%);
    border: 1px solid white;
    border-radius: 0px;
}



.widget-icon {
    color: #005248;
    font-size: 20px;
    background-color: rgba(114, 124, 245, 0.25);
    height: 40px;
    width: 40px;
    text-align: center;
    line-height: 40px;
    border-radius: 3px;
    display: inline-block;
}

.f-13{
    font-size: 13px;
}

.font-13{
    font-size: 13px;
}

.font-14{
    font-size: 14px;
}